const state = {
  apy_supply: "", // 存款APY 年化
  apy_borrow: "", // 借款APY 年化
  balance: "-", // 当前账户余额
};

const mutations = {
  set_WBTC_apy_supply(state, apy) {
    // console.log("--- set_WBTC_apy_supply ---", apy);
    state.apy_supply = apy;
  },
  set_WBTC_apy_borrow(state, apy) {
    // console.log("--- set_WBTC_apy_borrow ---", apy);
    state.apy_borrow = apy;
  },
};

const actions = {};

export default {
  state,
  mutations,
  actions,
};

