const state = {
    chainID: undefined, // 当前公链chainID
    address: "--- --- ---", // String 我的地址
    balance: "---", // 这个是挖矿的，目前还没定下来0531
    supplyBalance: '--',
    netAPY: '--',
    borrowBalance: '--',
    borrowed: '--',
    totalLimit: '--',
    borrowedLimitPercent: 0
};

const mutations = {
    set_chainID(state, chainID) {
        state.chainID = chainID;
    },
    set_address(state, address) {
        state.address = address;
    },
    set_balance(state, balance) {
        state.balance = balance;
    },
    set_supplyBalance(state, balance) {
        state.supplyBalance = balance;
    },
    set_netAPY(state, apy) {
        state.netAPY = apy;
    },
    set_borrowBalance(state, balance) {
        state.borrowBalance = balance;
    },
    set_borrowed(state, borrowed) {
        state.borrowed = borrowed;
    },
    set_totalLimit(state, totalLimit) {
        state.totalLimit = totalLimit;
    },
    set_borrowedLimitPercent(state, borrowedLimitPercent) {
        state.borrowedLimitPercent = borrowedLimitPercent;
    },
};

const actions = {};

export default {
    state,
    mutations,
    actions
}
